.yoyotyoo {
    margin-left: auto;
    margin-right: auto;
    object-fit: contain;
    display: block;
    width: 95%;
    margin-bottom: 40px;
  }
  
  .button-styling{
    background-color: black;
    color: white;
  }
  
  .button-styling:hover{
    background-color: white;
    color: black;
  }
  
  .home-main-gifs {
    display: flex;
    gap: 10px;
    margin-left: 50px;
    margin-right: 50px;
    object-fit: contain;
    overflow: scroll;
    margin-bottom: 50px;
  }
  
