  .title-for-home-main {
    font-weight: 900px;
    font-size: 2rem;
    font-family: "Work Sans", sans-serif;
    justify-content: center;
    text-align: center;
  }
  
  .nike-home-logo-1 {
    width: 340px;
    height: 500px;
    cursor: pointer;
  }
  
  .home-jorden-title {
    font-weight: 900;
    font-size: 3rem;
    padding-top: 50px;
    padding-left: 50px;
    padding-right: 50px;
    color: black;
    text-align: center;
    margin-bottom: 0px;
  }
  
  .home-jorden-info {
    text-align: center;
    margin: 0px;
    font-family: "Work Sans", sans-serif;
  }
  
  .shop-title {
    display: grid;
    place-items: center;
    text-align: center;
  }
  
  .shop-title p {
    height: 30px;
    border-radius: 25px;
    background-color: black;
    color: white;
    width: 90px;
    height: 25px;
    padding: 5px;
    border-radius: 25px;
    margin-bottom: 90px;
    margin-top: 20px;
    padding-top: 10px;
    font-family: "Work Sans", sans-serif;
  }
  
  .item-box-list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-left: auto;
    margin-right: auto;
    width: 900px;
    height: 100px;
    overflow: hidden;
    transition: all ease-in-out 1s;
  }

  .item-box-list:hover{
    height: 300px;
  }
  
  .item-box-list-one {
    list-style: none;
    font-size: small;
    font-family: "Noto Sans Khojki", sans-serif;
    font-weight: 500;
    color: rgb(104, 98, 98);
  }
  
  .item-box-list-one h4 {
    color: black;
  }
  
  .item-box-list-one li {
    padding-top: 5px;
    padding-bottom: 5px;
  }
  
  .nike-cards {
    display: flex;
    width: 60%;
    height: 250px;
    gap: 3px;
  }
  
  .nike-cards > img {
    width: 35%;
    height: 250px;
    align-self: 1/1;
    object-fit: cover;
    border-radius: 5px;
    transition: all ease 1s;
  }
  
  .nike-cards > img:hover {
    width: 100%;
    scale: 1.1;
  }
  
  .grid-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    padding: 10px;
    gap: 5px;
    margin: 10px;
  }
  
  .grid-sub-container:nth-child(1) {
    grid-row: span 2;
  }
  
  .grid-sub-container:nth-child(1):hover {
    scale: 1.1;
  }
  
  .grid-sub-container:nth-child(2):hover {
    scale: 1.1;
  }
  
  .grid-sub-container:nth-child(3):hover {
    scale: 1.1;
  }
  
  .grid-sub-container:nth-child(4) {
    grid-row: span 2;
  }
  
  .grid-sub-container:nth-child(4):hover {
    scale: 1.1;
  }
  
  .grid-sub-container:nth-child(5) {
    grid-column: span 2;
  }
  
  .grid-sub-container:nth-child(5):hover {
    scale: 1.1;
  }
  
  .trending-container {
    display: flex;
    object-fit: contain;
    width: 800px;
    height: 800px;
    margin-left: 50px;
    margin-right: 50px;
  }
  